<script>
    export let text;
    export let href;
    export let style;
    export let iconPath;
    export let isActive;
    export let emoji = "";
    export let title = undefined;
    export let disabled = false;
    export let doLocalizeText = true;
    let _class = "";
    export { _class as class };
    if(!href.startsWith('http')){
        console.error(`Caution: ${href} is a relative link and may not direct properly`);
    }
</script>

<a target="_blank" rel="noreferrer" {href}>
    <button
        class={`button-secondary ${_class} ${isActive ? "is-active" : ""}`}
        {style}
        {disabled}
        {title}
        on:click
        {...$$restProps}
    >
        {#if _class.includes('patreon')}
            <div class="patreon-emoji">{emoji}</div>
            <div class="patreon-emoji">{emoji}</div>
            <div class="patreon-emoji">{emoji}</div>
            <div class="patreon-emoji">{emoji}</div>
            <div class="patreon-emoji">{emoji}</div>
        {/if}
        <div class="button-inner">
            {#if iconPath}<div
                    class="icon"
                    style={`background-image: url('ui/icons/${iconPath}')`}
                />{/if}
            <span class="button-text">
                {emoji}{doLocalizeText ? globalThis.i18n(text) : text}
            </span>
        </div>
    </button>
</a>

<style lang="scss">
    .button-secondary {
        background: none;
        color: white;
        font-size: large;
        font-weight: bold;
        border-radius:4px;
        padding:4px 8px;
        cursor: pointer;
    }
    .button-secondary:hover {
        border: 1px solid white;
    }
    .button-secondary.patreon:hover {
        background-image: linear-gradient(to right, #483f8e, #d85dd8) !important;
    }
    button:disabled {
        cursor: not-allowed;
    }
    .button-inner {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
    }
    .justify-center .button-inner {
        justify-content: center;
    }
    .justify-center .button-text {
        width: 100%;
    }
    .icon {
        background-repeat: no-repeat;
        background-size: cover; /* Make the image cover the div */
        background-position: 50%; /* Center the image inside the div */
        margin-right: 8px;
        padding: 0 10px;
        filter: drop-shadow(2px 2px 0px rgba(0, 0, 0, 1.0));
    }
    .button-text {
        text-shadow: 2px 2px 2px #000000;
        white-space: nowrap;
    }
    .patreon-emoji {
      position: absolute;
      top: 50%;
      left: 0;
      font-size: 2rem;
      opacity: 0;
      transform: translateX(-140%);
      transition: transform 0.2s ease, opacity 0.2s ease;
      pointer-events: none;
    }
    .button-secondary.patreon:hover {
        .patreon-emoji{
            opacity: 1;
        }
        .patreon-emoji:nth-child(1) { transform: translate(110%, -40%); }
        .patreon-emoji:nth-child(2) { transform: translate(240%, 0%); }
        .patreon-emoji:nth-child(3) { transform: translate(430%, 30%); }
        .patreon-emoji:nth-child(4) { transform: translate(340%, -110%); }
        .patreon-emoji:nth-child(5) { transform: translate(552%, -8%); }
    }

</style>
