<script>
    import { onMount } from "svelte";
    import { get } from "svelte/store";
    import Button from "./Button.svelte";
    import IconLink from "./IconLink.svelte";
    import Lobby from "./Lobby.svelte";
    import NoServersMessage from "./NoServersMessage.svelte";
    import PlayerConfigHotseat from "./PlayerConfigHotseat.svelte";
    import {
        isInRoom,
        isConnecting,
        connected,
        syncConnectedWithPieState,
        disconnect,
        isDisconnecting,
    } from "./stores";
    import ButtonSecondary from "./ButtonSecondary.svelte";
    import LinkSecondary from "./LinkSecondary.svelte";
    export let setRoute;
    let isJoining = false;
    let urlSearchParams = new URLSearchParams(location.search);
    let wsUrl = urlSearchParams.get("pieUrl");
    let gameName = urlSearchParams.get("game");
    let gamePassword;
    let joinRoomErrorText = "";
    let connectionError = "";
    let numOfHotseatPlayers = 2;
    let difficultyHotseat = undefined
    let previousCustomUrls =
        globalThis.storageGet("previousCustomUrls") || "[]";
    try {
        previousCustomUrls = JSON.parse(previousCustomUrls);
    } catch (e) {
        console.error("Could not parse previousCustomUrls");
    }

    let localServerStatus = {
        starting: false,
        running: false,
    };
    let forceRerender = 0;
    window.refreshMenuMultiplayer = () => {
        console.log("Force refresh menu multiplayer");
        forceRerender += 1;
    };
    if (globalThis.steamworks) {
        globalThis.steamworks.subscribeToLobbyJoined(() => {
            $isConnecting = true;
            window.syncConnectedWithPieState(true);
        });
        globalThis.steamworks?.subscribeToLobbyDataUpdate(
            ({ lobby, member, success }) => {
                if (success) {
                    $isConnecting = true;
                    window.syncConnectedWithPieState(true);
                }
            },
        );
    } else {
        console.warn("No globalThis.steamworks");
    }

    function connect() {
        if (wsUrl) {
            console.log("Menu: Connect to server", wsUrl);
            $isConnecting = true;
            const url = new URL(location.href);
            url.searchParams.set("pieUrl", wsUrl);
            window.history.pushState(null, null, url);
            // Clear error now that we are rejoining
            connectionError = "";
            return window
                .connect_to_wsPie_server(wsUrl)
                .catch((e) => {
                    console.error("Connection error:", e);
                    connectionError = e;
                })
                .then(() => {
                    window.syncConnectedWithPieState(true);
                    if (get(connected)) {
                        try {
                            if (subRoute == CUSTOM_SERVER) {
                                previousCustomUrls.unshift(wsUrl);
                                // deduplicate
                                previousCustomUrls = previousCustomUrls.filter(
                                    (x, i) =>
                                        previousCustomUrls.indexOf(x) === i,
                                );
                                globalThis.storageSet(
                                    "previousCustomUrls",
                                    JSON.stringify(previousCustomUrls),
                                );
                            }
                        } catch (e) {
                            console.error(
                                "Could not save previousCustomUrls",
                                e,
                            );
                        }
                        connectionError = "";
                    }
                });
        }
        return Promise.reject("No wsUrl defined to connect to");
    }
    function leaveRoom() {
        window.pieLeaveRoom();
        $isInRoom = false;
    }
    function join(isHosting) {
        if (subRoute == P2P) {
            gameName = "p2p";
            // Ensure that pie is on p2p mode
            setPieToP2PMode(true);
        }
        if (gameName) {
            if (subRoute == P2P || window.isConnected()) {
                // Note: Must not join a room until pixi is setup
                console.log("Menu: Connect to game name", gameName);
                // Now that we are both connected to the pieServer and assets are loaded,
                // we can host or join a game
                isJoining = true;
                // Reset error
                joinRoomErrorText = "";
                // Host can't do a promise because the name clobber error
                // always occurs after a successful connection, so we must use a
                // callback.
                window.menuJoinErr = (err) => {
                    joinRoomErrorText = err;
                    $isInRoom = false;
                };
                // TODO: differentiate making room and joining room
                return window
                    .joinRoom(
                        { name: gameName, password: gamePassword },
                        isHosting,
                    )
                    .then(() => {
                        const url = new URL(location.href);
                        url.searchParams.set("game", gameName);
                        window.history.pushState(null, null, url);
                        $isInRoom = true;
                        isJoining = false;
                        globalThis.syncConnectedWithPieState(true);
                    })
                    .catch((e) => {
                        console.error("Could not join room", e);
                        joinRoomErrorText = e;
                        isJoining = false;
                        globalThis.syncConnectedWithPieState(false);
                    })
            } else {
                console.error(
                    "Cannot join room until pieClient is connected to a pieServer",
                );
            }
        } else {
            console.log("Cannot join game until a gameName is provided");
        }
        return Promise.resolve();
    }
    let serverListPromise;
    onMount(() => {
        window.syncConnectedWithPieState(true);
        // Get server list from server hub:
        serverListPromise = fetch(
            "https://server-hub-d2b2v.ondigitalocean.app/",
        )
            .then((response) => {
                return response.json();
            })
            .then((x) => {
                console.log("Serverlist:", x);
                return x;
            })
            .catch(console.error);
        // Subscribe to Local Server messages:
        console.log("Subscribe to local server messages");
        globalThis.localServer?.onStatus((_localServerStatus) => {
            console.log("Local server status", localServerStatus);
            localServerStatus = _localServerStatus;
            if (localServerStatus.running) {
                wsUrl = "ws://localhost:8080";
                connect();
            }
        });
        // For quickly joining a multiplayer game for testing
        const quickJoin = globalThis.storageGet("devQuickJoin");
        // If not already connected, quickjoin
        if (quickJoin && !get(connected)) {
            console.log("dev: Auto joining", quickJoin);

            window
                .connect_to_wsPie_server("ws://localhost:8080")
                .catch((e) => {
                    console.error("Connection error:", e);
                    connectionError = e;
                })
                .then(syncConnectedWithPieState)
                .then(() => {
                    gameName = quickJoin;
                    return join(true);
                })
                .then(() => {
                    globalThis.configPlayer({ lobbyReady: true });
                });
        }
        // Don't invoke this if we're returning to a different sub route
        if(!window.lastSubRoute || window.lastSubRoute == P2P ){
            // This is important because setting options route to p2p
            // setsPieToP2PMode
            setOptionsRoute(P2P);
        }
        
    });
    const SERVER_BROWSER = "SERVER_BROWSER";
    const CUSTOM_SERVER = "CUSTOM_SERVER";
    const P2P = "P2P";
    const LOCAL = "LOCAL";
    const HOTSEAT = "HOTSEAT";
    
    let subRoute = !!window.lastSubRoute
        ? window.lastSubRoute 
        : (document.body.classList.contains("pieIsInstanceOfPiePeer")
            ? P2P
            : SERVER_BROWSER);
    function setOptionsRoute(_subRoute) {
        console.log('set options route', _subRoute)
        window.lastSubRoute = _subRoute;
        // Disconnect from current server when changing route
        if (_subRoute !== subRoute) {
            wsUrl = "";
            window.intentionalDisconnect = true;
            disconnect('Disconnected due to menu navigation: ' + _subRoute);
            if (globalThis.setPieToP2PMode) {
                globalThis.setPieToP2PMode(_subRoute === P2P);
            } else {
                console.error(
                    "Critical error: no such function globalThis.setPieToP2PMode",
                );
            }
        }
        subRoute = _subRoute;
    }
</script>

<div class="flex height100">
    <aside class="height100">
        <div
            class="flex flex-direction-column justify-content-space-between height100"
        >
            <div class="flex flex-direction-column gap-15 pad-20">
                <div class="flex align-items-center gap-20">
                    <ButtonSecondary
                        on:click={() => {
                            window.goBack();
                        }}
                        type="button"
                        text="🠔 Back"
                        class="sm"
                    />
                    <!-- <h1 class="aside-title">
                        {globalThis.i18n("Multiplayer")}
                    </h1> -->
                </div>
                <div class="flex flex-direction-column gap-15">
                    {#if location.href.includes("localhost")}
                        <ButtonSecondary
                            on:click={() => {
                                // Dev helper button, quickly connect to local dev server
                                setOptionsRoute(CUSTOM_SERVER);
                                wsUrl = "ws://localhost:8080";
                                connect();
                            }}
                            text="localhost:8080"
                            type="button"
                            style="text-align:left"
                            doLocalizeText={false}
                        />
                    {/if}
                    <h2>{globalThis.i18n("Online Multiplayer")}</h2>
                    <div class="flex flex-direction-column gap-15 server-tab">
                        <div class="beta-tag-container">
                            <span class="beta-tag">New</span>
                            <ButtonSecondary
                                on:click={() => setOptionsRoute(P2P)}
                                isActive={subRoute == P2P}
                                type="button"
                                text={`${globalThis.i18n("Steam Player to Player")}`}
                                style="text-align:left;width:100%;"
                                doLocalizeText={false}
                            />
                        </div>
                        <ButtonSecondary
                            on:click={() => setOptionsRoute(SERVER_BROWSER)}
                            isActive={subRoute == SERVER_BROWSER}
                            type="button"
                            text={`${globalThis.i18n("Community Servers")}`}
                            style="text-align:left"
                            doLocalizeText={false}
                        />
                    </div>
                    <h2>{globalThis.i18n("Local Multiplayer")}</h2>
                    <div class="flex flex-direction-column gap-15 server-tab">
                        <ButtonSecondary
                            on:click={() => setOptionsRoute(LOCAL)}
                            isActive={subRoute == LOCAL}
                            type="button"
                            text={`${globalThis.i18n("Local Area Network")}`}
                            style="text-align:left"
                            doLocalizeText={false}
                        />
                        <ButtonSecondary
                            on:click={() => setOptionsRoute(HOTSEAT)}
                            isActive={subRoute == HOTSEAT}
                            type="button"
                            text={`${globalThis.i18n("Hotseat Multiplayer")}`}
                            style="text-align:left"
                            doLocalizeText={false}
                        />
                    </div>
                    <h2>{globalThis.i18n("Self Hosted")}</h2>
                    <div class="flex flex-direction-column gap-15 server-tab">
                        <ButtonSecondary
                            on:click={() => setOptionsRoute(CUSTOM_SERVER)}
                            isActive={subRoute == CUSTOM_SERVER}
                            type="button"
                            text={`${globalThis.i18n("Self Hosted Online Server")}`}
                            style="text-align:left"
                            doLocalizeText={false}
                        />
                    </div>
                </div>
            </div>
        </div>
    </aside>

    <main>
        {#if $isDisconnecting}
            <div class="connecting-spinner">
                <div class="lds-ellipsis">
                    <div />
                    <div />
                    <div />
                    <div />
                </div>
            </div>
        {:else}
            <div class="pad-20" style="min-width:400px;">
                {#if subRoute == HOTSEAT}
                    <div class="margin-bottom-15">
                        {globalThis.i18n("hotseat_description")}
                    </div>
                    <div class="flex gap-15 margin-bottom-15">
                        <Button
                            text={"Add Player"}
                            on:click={() => {
                                numOfHotseatPlayers += 1;
                            }}
                            style="width:auto;"
                        />
                        {#if numOfHotseatPlayers > 2}
                            <Button
                                text={"Remove Player"}
                                on:click={() => {
                                    numOfHotseatPlayers -= 1;
                                }}
                                style="width:auto;"
                            />
                        {/if}
                    </div>
                    {#each { length: numOfHotseatPlayers } as _, i}
                        <PlayerConfigHotseat
                            index={i}
                            name={`Player ${i + 1}`}
                        />
                    {/each}
                <h3>{globalThis.i18n('Choose a Difficulty')}</h3>
                <div class="flex-menu">
                    <ButtonSecondary
                        on:click={() => {
                            difficultyHotseat = undefined
                        }}
                        emoji="🧙‍♂️ "
                        isActive={difficultyHotseat==undefined}
                        text="Normal"
                    />
                    <ButtonSecondary
                        on:click={() => {
                            difficultyHotseat = 'hard'
                        }}
                        emoji="👹 "
                        text="Hard"
                        isActive={difficultyHotseat=='hard'}
                        disabled={false}
                    />
                    <ButtonSecondary
                        on:click={() => {
                            difficultyHotseat = 'impossible'
                        }}
                        emoji="💀 "
                        text="Impossible"
                        isActive={difficultyHotseat=='impossible'}
                        disabled={false}
                    />
                </div>
                    <Button
                        text={"Start Game"}
                        style="width:auto;"
                        iconPath="forward.png"
                        on:click={() => {
                            // Start music on button click to get past Firefox permissions
                            window.playMusic();
                            // Connect to solo
                            window
                                .startSingleplayer(numOfHotseatPlayers, difficultyHotseat)
                                .then(() => {
                                    window.syncConnectedWithPieState(true);
                                });
                        }}
                    />
                {:else}
                    {#if subRoute == SERVER_BROWSER}
                        <div class="margin-bottom-15">
                            {#await serverListPromise}
                                <p>...fetching servers</p>
                            {:then serverList}
                                <h2>{globalThis.i18n("Community Servers")}</h2>
                                <br/>
                                <div style="white-space:pre-line;color:#a296ff;">{globalThis.i18n('steam-lobbies-message')} </div>
                                <br/>
                                <br/>
                                {#if serverList}
                                    <div
                                        class={serverList?.message
                                            ? "margin-bottom-15 max-width-800"
                                            : ""}
                                    >
                                        {serverList?.message || ""}
                                    </div>
                                    {#if serverList && serverList.servers && serverList.servers.length !== 0}
                                        <table>
                                            <thead>
                                                <tr>
                                                    <th
                                                        >{globalThis.i18n(
                                                            "Name",
                                                        )}</th
                                                    >
                                                    <th
                                                        >{globalThis.i18n(
                                                            "Region",
                                                        )}</th
                                                    >
                                                    <th
                                                        >{globalThis.i18n(
                                                            "Version",
                                                        )}</th
                                                    >
                                                    <th />
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {#if serverList?.servers}
                                                    {#each serverList.servers.sort( (a, b) => {
                                                            if (!a || !a.appVersion || !b || !b.appVersion) {
                                                                return 0;
                                                            }
                                                            const [aMajor, aMinor, aPatch] = a.appVersion
                                                                    .split(".")
                                                                    .map(Number);
                                                            const [bMajor, bMinor, bPatch] = b.appVersion
                                                                    .split(".")
                                                                    .map(Number);

                                                            if (aMajor !== bMajor) {
                                                                return bMajor - aMajor;
                                                            } else if (aMinor !== bMinor) {
                                                                return bMinor - aMinor;
                                                            } else {
                                                                return bPatch - aPatch;
                                                            }
                                                        }, ) as server (server.url)}
                                                        {#if server}
                                                            <tr
                                                                class={wsUrl ==
                                                                    server.url &&
                                                                $connected
                                                                    ? "active"
                                                                    : ""}
                                                            >
                                                                <td
                                                                    >{server.name}</td
                                                                >
                                                                <td
                                                                    >{server.region}</td
                                                                >
                                                                <td
                                                                    >{server.appVersion
                                                                        ? `v${server.appVersion}`
                                                                        : "Unknown"}</td
                                                                >
                                                                <td>
                                                                    {#if wsUrl == server.url && $isConnecting}
                                                                        <div
                                                                            class="connecting-spinner"
                                                                        >
                                                                            <div
                                                                                class="lds-ellipsis"
                                                                            >
                                                                                <div
                                                                                />
                                                                                <div
                                                                                />
                                                                                <div
                                                                                />
                                                                                <div
                                                                                />
                                                                            </div>
                                                                        </div>
                                                                    {:else if $connected}
                                                                        {#if wsUrl == server.url}
                                                                            <div
                                                                                class="flex gap-15"
                                                                            >
                                                                                <Button
                                                                                    text={"Connect"}
                                                                                    disabled={true}
                                                                                    doLocalizeText={false}
                                                                                />
                                                                                <Button
                                                                                    on:click={disconnect}
                                                                                    text="Disconnect"
                                                                                />
                                                                            </div>
                                                                        {/if}
                                                                    {:else}
                                                                        <Button
                                                                            on:click={() => {
                                                                                wsUrl =
                                                                                    server.url;
                                                                                connect();
                                                                            }}
                                                                            text={"Connect"}
                                                                            class="connect-btn"
                                                                            doLocalizeText={false}
                                                                        />
                                                                    {/if}
                                                                </td>
                                                            </tr>
                                                        {/if}
                                                    {/each}
                                                {/if}
                                            </tbody>
                                        </table>
                                    {:else}
                                        <NoServersMessage />
                                    {/if}
                                {:else}
                                    <NoServersMessage />
                                {/if}
                            {:catch error}
                                <p style="color: red">{error.message}</p>
                            {/await}
                        </div>
                    {/if}
                    {#if subRoute == LOCAL}
                        <div class="flex gap-15 margin-bottom-15">
                            <Button
                                on:click={() => {
                                    globalThis.localServer.start();
                                }}
                                text="Host Local Server"
                            />
                            <Button
                                on:click={() => {
                                    globalThis.localServer.start();
                                }}
                                disabled={!localServerStatus.running}
                                text="Stop Local Server"
                            />
                        </div>
                        {#if localServerStatus.starting && !localServerStatus.running}
                            <div class="connecting-spinner">
                                <div class="connecting-text">
                                    {globalThis.i18n(
                                        "Starting local server",
                                    )}...
                                </div>
                                <div class="lds-ellipsis">
                                    <div />
                                    <div />
                                    <div />
                                    <div />
                                </div>
                            </div>
                        {/if}
                    {/if}
                    {#if subRoute !== SERVER_BROWSER && subRoute !== P2P}
                        <label for="server-url"
                            >{globalThis.i18n("Server Url")}</label
                        >
                        <div>
                            <input
                                id="server-url"
                                bind:value={wsUrl}
                                on:keypress={(e) => {
                                    e.key == "Enter" && connect();
                                }}
                            />
                            <div class="flex gap-15">
                                <Button
                                    on:click={connect}
                                    disabled={$connected}
                                    text="Connect"
                                />
                                <Button
                                    on:click={disconnect}
                                    disabled={!$connected}
                                    text="Disconnect"
                                />
                            </div>
                        </div>
                        {#if subRoute == CUSTOM_SERVER && !$connected}
                            <div
                                class="flex flex-direction-column gap-15"
                                style="margin-top:1em;"
                            >
                                <LinkSecondary
                                    href="https://youtu.be/8CTbL38eCvk"
                                    text="How to Host your own Server"
                                />
                                {#each previousCustomUrls as previousCustomUrl}
                                    <Button
                                        on:click={() => {
                                            // Dev helper button, quickly connect to local dev server
                                            setOptionsRoute(CUSTOM_SERVER);
                                            wsUrl = previousCustomUrl;
                                            connect();
                                        }}
                                        text={`🕒 ${previousCustomUrl}`}
                                        type="button"
                                        style="text-align:left; text-transform:none;"
                                        doLocalizeText={false}
                                    />
                                {/each}
                            </div>
                        {/if}
                    {/if}
                    {#if subRoute == P2P}
                        {#key forceRerender}
                            <div class="flex gap-15">
                                <Button
                                    class={$connected
                                        ? "pretend-disabled "
                                        : ""}
                                    on:click={() => {
                                        // true denotes that this is HOSTING not just joining
                                        join(true).then(() => {
                                            console.log(
                                                "Host: setting mods on server",
                                            );
                                            globalThis.saveActiveMods(
                                                globalThis.activeMods,
                                            );
                                        });
                                    }}
                                    text="Host Lobby"
                                />
                                <Button
                                    class={$connected
                                        ? ""
                                        : "pretend-disabled "}
                                    on:click={() => {
                                        globalThis.pie.leaveRoom();
                                    }}
                                    text="Exit Lobby"
                                />
                            </div>
                            <div class="error-inline">{joinRoomErrorText}</div>
                        {/key}
                    {/if}
                    {#if $isConnecting && !$connected}
                        <div class="connecting-spinner">
                            <div class="connecting-text">
                                {globalThis.i18n("Connecting")}...
                            </div>
                            <div class="lds-ellipsis">
                                <div />
                                <div />
                                <div />
                                <div />
                            </div>
                        </div>
                    {/if}
                    <div class="error-inline">{connectionError}</div>
                    {#if $connected && subRoute !== P2P}
                        <div class="flex gap-15">
                            <div style="width:65%;">
                                <label for="game-name"
                                    >{globalThis.i18n("Game name")}</label
                                >
                                <input
                                    id="game-name"
                                    bind:value={gameName}
                                    on:keypress={(e) => {
                                        e.key == "Enter" &&
                                            join(true && subRoute !== P2P);
                                    }}
                                    disabled={$isInRoom}
                                />
                            </div>
                            <div style="width:35%;">
                                <label for="game-password"
                                    >{globalThis.i18n(
                                        "Password (Optional)",
                                    )}</label
                                >
                                <input
                                    id="game-password"
                                    bind:value={gamePassword}
                                    on:keypress={(e) => {
                                        e.key == "Enter" &&
                                            join(true && subRoute !== P2P);
                                    }}
                                    disabled={$isInRoom}
                                />
                            </div>
                        </div>
                        {#if isJoining}
                            Joining...
                            <div class="lds-ellipsis">
                                <div />
                                <div />
                                <div />
                                <div />
                            </div>
                        {:else if $isInRoom}
                            <Button
                                on:click={leaveRoom}
                                text="Leave Room"
                                style="max-width:50%;"
                            />
                        {:else}
                            <div class="flex gap-15">
                                <Button
                                    on:click={() => {
                                        // true denotes that this is HOSTING not just joining
                                        join(true).then(() => {
                                            console.log(
                                                "Host: setting mods on server",
                                            );
                                            globalThis.saveActiveMods(
                                                globalThis.activeMods,
                                            );
                                        });
                                    }}
                                    text="Host"
                                />
                                <Button
                                    on:click={() => join(false)}
                                    text="Join"
                                />
                            </div>
                            <div class="error-inline">{joinRoomErrorText}</div>
                        {/if}
                    {/if}
                    {#if document.body.classList.contains("pieIsInstanceOfPiePeer") && $isConnecting}
                        <div class="connecting-spinner">
                            <div class="connecting-text">
                                {globalThis.i18n("Connecting")}...
                            </div>
                            <div class="lds-ellipsis">
                                <div />
                                <div />
                                <div />
                                <div />
                            </div>
                        </div>
                    {/if}
                    {#if $isInRoom || (document.body.classList.contains("pieIsInstanceOfPiePeer") && $connected)}
                        <Lobby {setRoute} {subRoute} />
                    {/if}
                {/if}
            </div>
        {/if}
    </main>
</div>

<style>
    .beta-tag-container {
        position: relative;
    }

    .beta-tag {
        position: absolute;
        top: 2px;
        right: 0;
        background-color: #483f8e;
        color: #ffffff;
        padding: 2px 6px;
        letter-spacing: 0.05em;
        transform: rotate(30deg);
        transform-origin: 50% 50%;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
        z-index: 10;
        border-radius: 4px;
    }
    .server-tab {
        margin-left: 15px;
    }
    table {
        border-collapse: collapse;
        border: 1px solid white;
    }
    thead tr {
        background-color: #483f8e;
        color: #ffffff;
        text-align: left;
    }

    table th,
    table td {
        padding: 12px 15px;
