<script>
    import PlayerConfig from "./PlayerConfig.svelte";
    import Button from "./Button.svelte";
    import ButtonSecondary from "./ButtonSecondary.svelte";
    import { PLAY } from "./routes";
    export let setRoute;
    let difficulty = undefined
</script>

<div id="main-menu">
    <div id="main-menu-inner" class="flex-menu">
        <div class="flex header">
            <ButtonSecondary
                on:click={() => window.goBack()}
                text="🠔 Back"
                class="justify-center"
            />
            <h1>{globalThis.i18n('Singleplayer')}</h1>
            <ButtonSecondary
                on:click={() => window.goBack()}
                style="visibility:hidden"
                text="🠔 Back"
                class="justify-center"
            />
        </div>
        <PlayerConfig useLargeClassPicker/>
        <h2>{globalThis.i18n('Choose a Difficulty')}</h2>
        <div class="flex-menu" style="width:150px">
            <ButtonSecondary
                on:click={() => {
                    difficulty = undefined
                }}
                emoji="🧙‍♂️ "
                isActive={difficulty==undefined}
                text="Normal"
            />
            <ButtonSecondary
                on:click={() => {
                    difficulty = 'hard'
                }}
                emoji="👹 "
                text="Hard"
                isActive={difficulty=='hard'}
                disabled={false}
            />
            <ButtonSecondary
                on:click={() => {
                    difficulty = 'impossible'
                }}
                emoji="💀 "
                text="Impossible"
                isActive={difficulty=='impossible'}
                disabled={false}
            />
        </div>
        <div style="width:250px">
            <Button
                on:click={() => {
                    // Connect to solo
                    window.startSingleplayer(1, difficulty).then(() => {
                        window.syncConnectedWithPieState(true);
                        setRoute(PLAY);
                    });
                }}
                
                emoji=""
                text="Play"
                class="justify-center"
                disabled={false}
            />
        </div>
    </div>
</div>

<style>
    h2 {
        margin: 0;
    }
    .flex-menu {
        display: flex;
        justify-content: center;
        align-items: center;
        gap:20px;
    }
    h1 {
        font-size: 3em;
    }
    .header {
        gap:230px;
        align-items:center;
    }

</style>