<script>
    export let src;
    export let alt;
    export let href;
    export let width;
    export let isActive;
    export let text = "";
    let _class = "";
    export { _class as class };
    if(!href.startsWith('http')){
        console.error(`Caution: ${href} is a relative link and may not direct properly`);
    }
</script>

<a target="_blank" rel="noreferrer" {href}>
    <button
        class={`button-wrapper ${_class} ${isActive ? "is-active" : ""}`}
        on:click
        {...$$restProps}
    >
        <div>
            <img {src} {width} {alt} />
            {#if text !== ""}
                &nbsp;
                <span>{text}</span>
            {/if}
        </div>
    </button>
</a>

<style lang="scss">
    button > div {
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    img {
        // Ensure image doesn't make button grow
        max-height: 100%;
    }
</style>
