<script>
  console.log("Menu: Svelte menu is running");
  import { onMount, onDestroy } from "svelte";
  import Mods from "./Mods.svelte";
  import Credits from "./Credits.svelte";
  import Load from "./Load.svelte";
  import Help from "./Help.svelte";
  import Encyclopedia from "./Encyclopedia.svelte";
  import Options from "./Options.svelte";
  import Pie from "./Pie.svelte";
  import Localization from "./Localization.svelte";
  import PageInProgress from "./PageInProgress.svelte";
  import {
    PLAY,
    OPTIONS,
    CREDITS,
    MODS,
    HELP,
    TODO,
    LOCALIZATION,
    MULTIPLAYER_SERVER_CHOOSER,
    LOAD_GAMES,
    ENCYCLOPEIDA,
    SINGLE_PLAYER_OPTIONS,
    SINGLE_PLAYER_MENU,
  } from "./routes";
  import Multiplayer from "./Multiplayer.svelte";
  import PrivacyPolicyPopup from "./PrivacyPolicyPopup.svelte";
  import SinglePlayerOptions from "./SinglePlayerOptions.svelte";
  import SinglePlayerMenu from "./SinglePlayerMenu.svelte";

  let route;
  let inGame = false;
  let forceRerender = 0;
  if(!window.lastRouteStack)
    window.lastRouteStack = []
  // Used when changing the language to ensure the menu
  // updates to the new language
  window.refreshMenu = () => {
    console.log("Force refresh menu");
    forceRerender += 1;
  };
  window.updateInGameMenuStatus = () => {
    inGame = window.player !== undefined;
  };
  setRoute(PLAY);
  function setRoute(r, noHistory = false) {
    if(route == r){
      console.log('Ignore setting route to same route', r);
      return;
    }
    if(!noHistory && route){
      window.lastRouteStack.push(route);
    }
    console.log("Menu: Route:", r, 'from:', route);
    document.body.classList.toggle(`route-${route}`, false);
    route = r;
    window.updateInGameMenuStatus();
    document.body.classList.toggle(`route-${route}`, true);
  }
  window.goBack = function goBack() {
    if(window.lastRouteStack.length)
      setRoute(window.lastRouteStack.pop(), true);
    else
      setRoute(PLAY);
  };
  function keydownListener(event) {
    // Ignore if not on menu
    if(!document.body.classList.contains('view-Menu'))
      return;

    switch (event.code) {
      case "Escape":
        goBack();
        break;
    }
  }

  let updateProgress = 0;
  // Start with updateComplete set to true so that the update UI will
  // ONLY EVER hide the play ui if update messages start coming through
  let updateComplete = true;
  onMount(() => {
    document.body.addEventListener("keydown", keydownListener);
  });

  onDestroy(() => {
    document.body.removeEventListener("keydown", keydownListener);
  });
  window.setMenu = setRoute;
  window.getMenuRoute = () => route;
</script>

<PrivacyPolicyPopup />
<div class="bg background"></div>
<div class="bg vampire"></div>
<div class="bg red"></div>
<div class="bg priest"></div>
<div class="bg blue"></div>
<div class="bg golem"></div>
<div class="bg green"></div>
<div class="bg cover"></div>
{#key forceRerender}
  <div id="menu">
    <div class="full-fill">
      <div class="decorative-border">
        {#if route == PLAY}
          <div id="main-menu">
            <div id="main-menu-inner">
              <img
                id="logo"
                src="ui/logo.png"
                alt="Spellmasons logo"
                width="800"
                draggable="false"
              />
              {#if updateComplete}
                <Pie {setRoute} {inGame} />
              {:else}
                <span>Update progress:</span>
                <div class="progress">
                  <div
                    style={`width:${Math.max(
                      // Always give enough room to show the percentage text
                      15,
                      Math.floor(updateProgress * 100),
                    )}%`}
                  >
                    {Math.floor(updateProgress * 100)}% &nbsp;
                  </div>
                </div>
              {/if}
            </div>
          </div>
        {:else if route == SINGLE_PLAYER_MENU}
          <SinglePlayerMenu route {setRoute}/>
        {:else if route == SINGLE_PLAYER_OPTIONS}
          <SinglePlayerOptions route {setRoute}/>
        {:else if route == MULTIPLAYER_SERVER_CHOOSER}
          <Multiplayer route {setRoute} />
        {:else if route == LOCALIZATION}
          <Localization route />
        {:else if route == LOAD_GAMES}
          <Load route {setRoute} />
        {:else if route == OPTIONS}
          <Options route {setRoute} />
          <!-- {:else if route == MAGE_TYPE}
          <MageTypeChooser route {setRoute} /> -->
        {:else if route == CREDITS}
          <Credits route />
        {:else if route == MODS}
          <Mods route />
        {:else if route == HELP}
          <Help route />
        {:else if route == ENCYCLOPEIDA}
          <Encyclopedia route {setRoute} />
        {:else if route == TODO}
          <PageInProgress route />
        {/if}
      </div>
    </div>
  </div>
{/key}

