<script>
    import Toggle from "./Toggle.svelte";
    import { onMount } from "svelte";
    import { lobbyPlayerList } from "./stores";
    import ButtonSecondary from "./ButtonSecondary.svelte";
    export let useLargeClassPicker
    // storage ids from config.ts in Golems
    let name = globalThis.storageGet("player-name");
    let color = `#${parseInt(globalThis.storageGet("player-color")).toString(
        16
    )}`;
    let wizardType = (globalThis.player !== undefined && globalThis.player.wizardType) || globalThis.storageGet("wizard-type");
    let familiar = globalThis.storageGet("familiar");
    // let colorMagic = `#${parseInt(
    //     globalThis.storageGet("player-color")
    // ).toString(16)}`;
    const convertToHex = (cssColor) => parseInt(cssColor.slice(1), 16);
    function sendConfig(color, colorMagic, name, wizardType, familiar) {
        window.configPlayer({
            color: convertToHex(color),
            colorMagic: convertToHex(colorMagic),
            name,
            wizardType,
            familiar,
        });
        // Update lobbyPlayerList
        $lobbyPlayerList = globalThis.lobbyPlayerList;
    }
    onMount(() => {
        // Temporary work around, since all self messages are handled immediately,
        // the config is being overwritten when underworld sends SYNC_PLAYERS due to
        // onClientConnected even tho this messages is processed after so I'm adding
        // a delay; however, the real solution is to update wsPie to add a flag that
        // says sendToSelfThroughServer so it doesn't handle it immediately
        setTimeout(() => {
            sendConfig(color, color, name);
        }, 200);
    });
</script>

{#if !useLargeClassPicker}
    <div class="flex gap-15 margin-bottom-15 player-config">
        <div class="flex flex-direction-column gap-15">
            <label for="player-name">{globalThis.i18n("Player Name")}</label>
            <input
                type="text"
                name="player-name"
                style="height:100%"
                bind:value={name}
                on:input={() => {
                    sendConfig(color, color, name);
                }}
            />
        </div>
        <div class="flex flex-direction-column gap-15">
            <label for="robe">{globalThis.i18n("Robe Color")}</label>
            <input
                type="color"
                name="robe"
                style="height:100%; padding:0;"
                bind:value={color}
                on:blur={() => {
                    sendConfig(color, color, name);
                }}
            />
        </div>
        <div class="flex flex-direction-column gap-15">
            <span>{globalThis.i18n("Wizard Type")}</span>
            <div class="flex flex-direction-row gap-15">
                {#each ['Spellmason', 'Deathmason', 'Goru'] as chooseWizardType}
                    <ButtonSecondary
                        on:click={() => {
                            wizardType = chooseWizardType;
                            sendConfig(color, color, name, chooseWizardType);
                        }}
                        on:keypress={() => {
                            wizardType = chooseWizardType;
                            sendConfig(color, color, name, chooseWizardType);
                        }}
                        class="justify-center"
                        text={chooseWizardType}
                        isActive={wizardType == chooseWizardType}
                    ></ButtonSecondary>
                {/each}
            </div>
        </div>
        <div class="flex flex-direction-column gap-15">
            <span>{globalThis.i18n("Familiar")}</span>
            <div class="flex flex-direction-row gap-15">
                    <ButtonSecondary
                        on:click={() => {
                            familiar = "";
                            sendConfig(color, color, name, undefined, familiar);
                        }}
                        on:keypress={() => {
                            familiar = "";
                            sendConfig(color, color, name, undefined, familiar);
                        }}
                        class="justify-center"
                        text={"None"}
                        isActive={familiar == ""}
                    ></ButtonSecondary>
                {#each globalThis.allFamiliars as chooseFamiliar}
                    <ButtonSecondary
                        on:click={() => {
                            familiar = chooseFamiliar;
                            sendConfig(color, color, name, undefined, chooseFamiliar);
                        }}
                        on:keypress={() => {
                            familiar = chooseFamiliar;
                            sendConfig(color, color, name, undefined, chooseFamiliar);
                        }}
                        class="justify-center"
                        text={chooseFamiliar}
                        isActive={familiar == chooseFamiliar}
                    ></ButtonSecondary>
                {/each}
            </div>
        </div>
    </div>
{/if}
{#if useLargeClassPicker}
    <h2>{globalThis.i18n('Pick your Character')}</h2>
    <div class="flex gap-15">
        {#each ['Spellmason', 'Deathmason', 'Goru'] as chooseWizardType}
            <div class="flex flex-direction-column classPicker">
                <img
                    class="character-select {wizardType == chooseWizardType ? '' : 'grayscale'}"
                    on:keypress={() => {
                        wizardType = chooseWizardType;
                        sendConfig(color, color, name, chooseWizardType);
                    }}
                    on:click={() => {
                        wizardType = chooseWizardType;
                        sendConfig(color, color, name, chooseWizardType);
                    }}
                    width="400"
                    height="400"
                    src={`images/character-select-${chooseWizardType.toLowerCase()}.png`}
                    alt={`Character select: ${chooseWizardType}`}
                    draggable="false"
                />
                <ButtonSecondary
                    on:click={() => {
                        wizardType = chooseWizardType;
                        sendConfig(color, color, name, chooseWizardType);
                    }}
                    on:keypress={() => {
                        wizardType = chooseWizardType;
                        sendConfig(color, color, name, chooseWizardType);

                    }}
                    class="justify-center"
                    text={chooseWizardType}
                    isActive={wizardType == chooseWizardType}
                ></ButtonSecondary>
            </div>
        {/each}
    </div>
{/if}

<style>
    h2 {
        margin: 0;
    }
    .classPicker {
        display: flex;
        gap:8px;
        text-align: center;
        font-size: 2em;
        font-weight: bold;
    }
    .character-select {
        cursor: pointer;
    }
    /* .grayscale {
        filter: grayscale(100%);
    }
    .grayscale:hover {
        filter:none;
    }*/
    .player-config{
        filter: drop-shadow(0px 0px 10px #000000);
    }
</style>