import { writable } from 'svelte/store';

export const lobbyPlayerList = writable([]);
export const connected = writable(false);
export const isInRoom = writable(false);
export const isConnecting = writable(false);
export const isDisconnecting = writable(false);
export function syncConnectedWithPieState(expectConnected) {
    const _connected = window.isConnected()
    // TODO Shouldn't call this after setting isConnecting because it will immediately turn it off
    // instead, don't set isConnecting if already connected
    connected.set(_connected);
    if (!_connected) {
        isInRoom.set(false);
    }
    // Disable loading state if connected state matches expectation
    if(expectConnected == _connected){
        isConnecting.set(false);
        isDisconnecting.set(false);
    }
}
globalThis.syncConnectedWithPieState = syncConnectedWithPieState;
globalThis.setMenuIsInRoom = inRoom => {
    isInRoom.set(inRoom);
    globalThis.syncConnectedWithPieState(inRoom);
}
export function disconnect(disconnectReason) {
    isDisconnecting.set(true);
    window.intentionalDisconnect = true;
    return window.pieDisconnect(disconnectReason).then(() => syncConnectedWithPieState(false));
}