<script>
    import Button from "./Button.svelte";
    import License from "./License.svelte";
    import PrivacyPolicy from "./PrivacyPolicy.svelte";
</script>

<div id="privacy-policy-popup">
    <div id="exit-btn">
        <Button
            on:click={() => {
                window.fullyExitGame();
            }}
            type="button"
            text={"X"}
            style="width:65px"
        />
    </div>
    <img
    id="logo"
    src="ui/logo.png"
    alt="Spellmasons logo"
    width="600"
    draggable="false"
    />
    <div id="legal-content" class="flex">
        <div class="legal">
            <PrivacyPolicy />
        </div>
        <div class="legal">
            <License />
        </div>
    </div>
    <div style="margin: 0 auto;">
        <input type="checkbox" id="opt-in-remote-logging" name="opt-in-remote-logging" />
        <label for="opt-in-remote-logging">Allow remote logging to assist with game statistics and debugging</label>
    </div>
    <div id="accept-btn-wrapper">
        <Button
            text="Accept"
            class="accept-btn justify-center"
            on:click={() => {
                if (globalThis.acceptPrivacyPolicyAndEULA) {
                    // Must accept before setting storage
                    globalThis.acceptPrivacyPolicyAndEULA();
                    const checkbox = document.getElementById('opt-in-remote-logging');
                    if (checkbox.checked) {
                        globalThis.storageSet(
                            "remote-logging-opt-in",
                            "yes",
                        );
                    }
                } else {
                    console.error(
                        "Unexpected: No acceptPrivacyPolicyAndEULA function",
                    );
                }
            }}
        />
    </div>
</div>

<style global>
    .tldr {
        font-size: 1.2em;
        text-align: center;
        max-width: 600px;
        margin: 0 auto;
    }
    .tldr span {
        font-size: 1rem;
    }
    .accept-btn {
        max-width: 240px;
    }
    #accept-btn-wrapper {
        display: flex;
        width: 100%;
        justify-content: center;
        margin: 1em 0 2em 0;
    }
    #privacy-policy-popup #logo {
        margin:8px auto 0 auto;
        width:600px;
    }
    #exit-btn {
        position: absolute;
        top:8px;
        right:8px;
        z-index: 1000;
        margin: 2em;
        display: flex;
        flex-direction: row-reverse;
    }
    #privacy-policy-popup {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        display: none;
        background-color: #171717;
        /* Show over floating logo*/
        z-index: 2;
    }
    #privacy-policy-popup.show {
        display: flex;
        flex-direction: column;
        gap: 1em;
    }
    #legal-content {
        background-color: #171717;
        gap: 2em;
        width: 80%;
        margin: 0 auto;
        overflow: hidden;
    }
    #legal-content .legal {
        color: #ffffff !important;
        background: #252525;
        width: 50%;
        overflow-y: scroll;
        padding: 2em;
        user-select: text;
    }
</style>
