<script>
    import { onMount, onDestroy } from "svelte";
    import Button from "./Button.svelte";
    import PlayerConfig from "./PlayerConfig.svelte";
    import { lobbyPlayerList } from "./stores";
    import { LOAD_GAMES, MODS } from "./routes";
    import ButtonSecondary from "./ButtonSecondary.svelte";
    import {
        isConnecting,
    } from "./stores";
    export let setRoute;
    export let subRoute;
    let intervalId;
    onMount(() => {
        setInterval(() => {
            // Update lobbyPlayerList
            $lobbyPlayerList = globalThis.lobbyPlayerList;
        }, 500);
    });
    onDestroy(() => {
        clearInterval(intervalId);
    });
    window.rerenderGameDifficultyButtons = () => {
        forceRerenderGameDifficulty += 1;
    }
    let forceRerenderGameDifficulty=0;
    let forceRerender = 0;
</script>

<h1>{globalThis.i18n("Game Lobby")}</h1>
<div class="flex gap-15 flex-direction-column">
    {#if $lobbyPlayerList[0]?.clientId == globalThis.clientId}
        <h3>{globalThis.i18n("Difficulty")}</h3>
        <div class="flex gap-15">
            {#key forceRerenderGameDifficulty}
                <ButtonSecondary
                    on:click={() => {
                        globalThis.setDifficulty("");
                    }}
                    text="Normal"
                    disabled={$lobbyPlayerList[0]?.clientId != globalThis.clientId}
                    style="flex:0 0 0;"
                    isActive={globalThis.devUnderworld.gameMode == "" || globalThis.devUnderworld.gameMode == undefined}
                />
                <ButtonSecondary
                    on:click={() => {
                        globalThis.setDifficulty("hard");
                    }}
                    text="Hard"
                    disabled={$lobbyPlayerList[0]?.clientId != globalThis.clientId}
                    style="flex:0 0 0;"
                    isActive={globalThis.devUnderworld.gameMode == "hard"}
                />
                <ButtonSecondary
                    on:click={() => {
                        globalThis.setDifficulty("impossible");
                    }}
                    text="Impossible"
                    disabled={$lobbyPlayerList[0]?.clientId != globalThis.clientId}
                    style="flex:0 0 0;"
                    isActive={globalThis.devUnderworld.gameMode == "impossible"}
                />
            {/key}
        </div>
        <h3>{globalThis.i18n("Controls")}</h3>
        <div class="flex gap-15">
            <ButtonSecondary
                on:click={() => setRoute(LOAD_GAMES)}
                text="Load Saved Game"
                iconPath="open.png"
                disabled={$lobbyPlayerList[0]?.clientId != globalThis.clientId}
                title="Only the host may Load a saved game for a multiplayer lobby"
                style="flex:0 0 0;"
            />
            <ButtonSecondary
                on:click={() => setRoute(MODS)}
                text="Mods"
                class="justify-center sm"
                disabled={$lobbyPlayerList[0]?.clientId != globalThis.clientId}
                title="Only the host may set mods"
            />
        </div>
                        
        {#if subRoute == 'P2P'}
            <div>
                <!-- <div class="hub-connection-status"></div>
                <button class="openLobbyBtn" data-fn="openlobby"></button> -->
                <Button
                    on:click={() => {
                        globalThis.electronSettings.openInviteDialog();
                    }}
                    type="button"
                    text="Invite Friends"
                    style="max-width: 140px"
                />
            </div>
        {/if}
    {/if}
    {#key forceRerender}
        <Button
            on:click={() => {
                globalThis.configPlayer({ lobbyReady: true });
                forceRerender+=1;
            }}
            type="button"
            text="Ready"
            class="sm"
        />
    {/key}
    {#if !$isConnecting}
        <div style="margin-top:15px;">
            <PlayerConfig />
        </div>
    {/if}
    <h2>{globalThis.i18n("Players")}</h2>
    <table id="lobby-player-list">
        <thead>
            <tr />
        </thead>
        <tbody>
            {#each $lobbyPlayerList.sort((a, _b) => {
                // Sort self to the top
                if (a === globalThis.player) {
                    return -1;
                } else {
                    return 0;
                }
            }) as { name, status, clientId, clientConnected, color, ready, gameVersion }}
                <tr>
                    <td>{name}</td>
                    <td>{status}</td>
                    <td
                        class="lobby-player-color"
                        style={`background-color:${color};`}
                    />
                    <td>
                        {ready}
                    </td>
                    {#if clientConnected}
                    <td>
                        {#if (globalThis.player && clientId != globalThis.player.clientId && globalThis.isHost(globalThis.pie))}
                            <ButtonSecondary
                                on:click={() => {
                                    globalThis.kickPeer(clientId, name || clientId);
                                }}
                                type="button"
                                text={`${globalThis.i18n('kick-player')}`}></ButtonSecondary>
                        {/if}
                    </td>
                    <td>
                        {#if gameVersion && gameVersion != globalThis.SPELLMASONS_PACKAGE_VERSION}
                        <span style="color:red;">VERSION MISMATCH: {gameVersion} does not equal {globalThis.SPELLMASONS_PACKAGE_VERSION}.  Players must play on the same game version or else desync issues will occur.</span>
                        {/if}
                    </td>
                    {/if}
                    {#if !clientConnected && !document.body.classList.contains('pieIsInstanceOfPiePeer')}
                        <td>
                            <ButtonSecondary
                                on:click={() => {
                                    window.pieInhabitPlayer(clientId);
                                }}
                                type="button"
                                text="Join Game As Player"
                            />
                        </td>
                    {/if}
                </tr>
            {/each}
        </tbody>
    </table>
    <div class="request-to-join-p2p"></div>
</div>

<style>
    #lobby-player-list {
        min-width: 500px;
    }
    .lobby-player-color {
        width: 20px;
        height: 18px;
    }
    h2, h3 {
        margin: 0;
    }
</style>
