<h1 class="c14" id="h.squ21wrma4y2"><span class="c2 c12">Privacy Policy</span></h1>
    <p class="c1"><span class="c15">Last updated: August 1, 2025</span></p>
    <p class="c1"><span class="c3">This Privacy Policy explains how Octavia Blue LLC (&quot;we&quot;, &quot;us&quot;, or
            &quot;our&quot;) handles data when you play our game, Spellmasons.</span></p>
    <p class="c1"><span class="c6 c2">Contact Information:</span></p>
    <ul class="c9 lst-kix_ioqhzfbc1sqf-0 start">
        <li class="c0 li-bullet-0"><span class="c3">Email: spellmasons@gmail.com</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Address: 539 W. Commerce St #4792, Dallas, TX, 75208</span></li>
    </ul>
    <h2 class="c8" id="h.59kyvkqfz462"><span class="c4 c2">1. What We Collect</span></h2>
    <p class="c1"><span class="c3">We collect only the following categories of personal information:</span></p>
    <h3 class="c5" id="h.q6hdzsrsq8bk"><span class="c7 c2">Game Logs and Error Reports</span></h3>
    <ul class="c9 lst-kix_qo4i3vw0uq14-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Data collected:</span><span class="c3">&nbsp;IP addresses, device
                information, error messages, crash reports, gameplay data</span></li>
        <li class="c0 li-bullet-0"><span class="c2">CCPA category:</span><span class="c3">&nbsp;Identifiers, Internet or
                other electronic network activity information</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Purpose:</span><span class="c3">&nbsp;Diagnosing bugs, crashes, and
                unexpected behavior</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Legal basis (GDPR):</span><span class="c3">&nbsp;Legitimate
                interests (improving game functionality and user experience)</span></li>
    </ul>
    <h3 class="c5" id="h.v4ldwxoy2m2h"><span class="c2 c7">Steam Identifiers</span></h3>
    <ul class="c9 lst-kix_ixtw1ro6t448-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Data collected:</span><span class="c3">&nbsp;SteamIDs and related
                Steam platform identifiers</span></li>
        <li class="c0 li-bullet-0"><span class="c2">CCPA category:</span><span class="c3">&nbsp;Identifiers</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Purpose:</span><span class="c3">&nbsp;Facilitating multiplayer
                connections and Steam platform integration</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Legal basis (GDPR):</span><span class="c3">&nbsp;Performance of
                contract (providing multiplayer gaming services)</span></li>
    </ul>
    <h2 class="c8" id="h.b6ekyhmvuuba"><span class="c4 c2">2. How We Use Your Information</span></h2>
    <p class="c1"><span class="c3">We use your personal information for the following business purposes:</span></p>
    <h3 class="c5" id="h.r62iyjo9x6bv"><span class="c7 c2">Primary Uses</span></h3>
    <ul class="c9 lst-kix_cpyd37l3cr5o-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Fix bugs and improve game performance:</span><span
                class="c3">&nbsp;We analyze error logs to identify and resolve technical issues</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Facilitate multiplayer gameplay:</span><span class="c3">&nbsp;Steam
                identifiers enable peer-to-peer connections</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Understand error frequency:</span><span class="c3">&nbsp;We track
                how often specific issues occur to prioritize fixes</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Analyze gameplay patterns:</span><span class="c3">&nbsp;We review
                anonymized gameplay data to improve game design</span></li>
    </ul>
    <h3 class="c5" id="h.bafpdo96yvfu"><span class="c7 c2">What We Don&#39;t Do</span></h3>
    <p class="c1"><span>We do </span><span class="c2">not</span><span class="c3">&nbsp;use your data for:</span></p>
    <ul class="c9 lst-kix_8jsskk1yyok3-0 start">
        <li class="c0 li-bullet-0"><span class="c3">Marketing or advertising</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Tracking across other websites or services</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Creating detailed user profiles</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Selling or sharing data for commercial purposes</span></li>
    </ul>
    <h2 class="c8" id="h.49b2petxq4xb"><span class="c4 c2">3. Legal Basis for Processing (GDPR)</span></h2>
    <ul class="c9 lst-kix_x3tm612oaykm-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Legitimate Interests:</span><span class="c3">&nbsp;For error logging
                and game improvement (we have determined our legitimate business interests do not override your privacy
                rights)</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Contract Performance:</span><span class="c3">&nbsp;For multiplayer
                features that are part of the gaming service you requested</span></li>
    </ul>
    <h2 class="c8" id="h.v7vn446w5icm"><span class="c2 c13">4. Data Retention</span></h2>
    <ul class="c9 lst-kix_e1hfhhl5wem0-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Error logs and crash reports:</span><span class="c3">&nbsp;Retained
                for up to 12 months, then automatically deleted</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Steam identifiers:</span><span class="c3">&nbsp;Retained only during
                active gaming sessions and deleted when session ends</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Aggregated/anonymized data:</span><span class="c3">&nbsp;May be
                retained indefinitely as it cannot identify individuals</span></li>
    </ul>
    <h2 class="c8" id="h.pyxxsksvd5wl"><span class="c4 c2">5. Third-Party Services and Data Sharing</span></h2>
    <p class="c1"><span class="c3">We work with the following third parties:</span></p>
    <h3 class="c5" id="h.puu6pr33mygw"><span class="c7 c2">Steamworks (Valve Corporation)</span></h3>
    <ul class="c9 lst-kix_iyajntq0u6dv-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Data shared:</span><span class="c3">&nbsp;Steam identifiers</span>
        </li>
        <li class="c0 li-bullet-0"><span class="c2">Purpose:</span><span class="c3">&nbsp;Game distribution and
                multiplayer functionality</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Their privacy policy:</span><span
                class="c3">&nbsp;https://store.steampowered.com/privacy_agreement/</span></li>
    </ul>
    <h3 class="c5" id="h.vz5f9f5xksg8"><span class="c7 c2">Digital Ocean</span></h3>
    <ul class="c9 lst-kix_v4f9bb35v6lx-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Data shared:</span><span class="c3">&nbsp;Technical connection data
                (may include IP addresses), game logs and error reports</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Purpose:</span><span class="c3">&nbsp;Providing dedicated community
                servers for multiplayer games and utilizing cloud storage for game logs and error reports</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Their privacy policy:</span><span
                class="c3">&nbsp;https://www.digitalocean.com/legal/privacy-policy/</span></li>
    </ul>
    <h3 class="c5" id="h.y2n8g4dona3b"><span class="c7 c2">Peer-to-Peer Networking</span></h3>
    <p class="c1"><span class="c3">During multiplayer sessions, your Steam ID and IP address may be temporarily visible
            to other players through Steam&#39;s P2P system. This is necessary for direct game connections.</span></p>
    <h2 class="c8" id="h.5s108913avo8"><span class="c2 c4">6. International Data Transfers</span></h2>
    <p class="c1"><span class="c3">If you are located outside the United States, your information may be transferred to
            and processed in the United States. For transfers of personal data from the European Economic Area (EEA),
            UK, and Switzerland, we rely on appropriate safeguards, such as the Standard Contractual Clauses (SCCs)
            and/or other data transfer mechanisms, as provided by our third-party service providers, including DigitalOcean (which provides SCCs in its DPA), and, where applicable, platforms such as Steam (Valve Corporation), which operate under similar international data protection standards.</span></p>
    <h2 class="c8" id="h.apds01xra5xy"><span class="c4 c2">7. Your Privacy Rights</span></h2>
    <p class="c1"><span class="c3">Depending on your location, you may have the following rights:</span></p>
    <h3 class="c5" id="h.wstiffxf170a"><span class="c7 c2">GDPR Rights (EU/UK residents)</span></h3>
    <ul class="c9 lst-kix_ugbh7sppktzm-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Access:</span><span class="c3">&nbsp;Request a copy of personal data
                we hold about you</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Rectification:</span><span class="c3">&nbsp;Request correction of
                inaccurate personal data</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Erasure:</span><span class="c3">&nbsp;Request deletion of your
                personal data</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Restriction:</span><span class="c3">&nbsp;Request limitation of
                processing</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Data Portability:</span><span class="c3">&nbsp;Request transfer of
                your data in a portable format</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Object:</span><span class="c3">&nbsp;Object to processing based on
                legitimate interests</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Withdraw Consent:</span><span class="c3">&nbsp;Where processing is
                based on consent</span></li>
    </ul>
    <h3 class="c5" id="h.98k2kfifolaq"><span class="c7 c2">CCPA Rights (California residents)</span></h3>
    <ul class="c9 lst-kix_jjq20cj38k5z-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Know:</span><span class="c3">&nbsp;Request disclosure of what
                personal information we collect, use, and share</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Delete:</span><span class="c3">&nbsp;Request deletion of personal
                information we&#39;ve collected</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Opt-Out:</span><span class="c3">&nbsp;Opt out of the
                &quot;sale&quot; or &quot;sharing&quot; of personal information (we do not sell or share personal
                information)</span></li>
        <li class="c0 li-bullet-0"><span class="c2">Non-Discrimination:</span><span class="c3">&nbsp;We will not
                discriminate against you for exercising your rights</span></li>
    </ul>
    <h3 class="c5" id="h.7hifwykbts8p"><span class="c7 c2">How to Exercise Your Rights</span></h3>
    <p class="c1"><span>To exercise any of these rights, email us at: </span><span
            class="c2 c6">spellmasons@gmail.com</span></p>
    <p class="c1"><span class="c3">Include in your request:</span></p>
    <ul class="c9 lst-kix_huzxwk54z2gt-0 start">
        <li class="c0 li-bullet-0"><span class="c3">Your Steam username (if applicable)</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Specific right you wish to exercise</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Any additional information to help us locate your data</span></li>
    </ul>
    <p class="c1"><span class="c3">We will respond to your request within the time frame required by applicable law,
            which is typically 30 days under GDPR and 45 days under CCPA, subject to any permitted extensions.</span>
    </p>
    <h2 class="c8" id="h.l7jcbxuiuosk"><span class="c4 c2">8. Children&#39;s Privacy</span></h2>
    <p class="c1"><span class="c3">Our game is not directed to children under 16. We do not knowingly collect personal
            information from children under 16. If we become aware that we have collected personal information from a
            child under 16, we will delete it promptly.</span></p>
    <p class="c1"><span class="c3">For users between 16-18, we recommend parental guidance when playing online
            multiplayer games.</span></p>
    <h2 class="c8" id="h.n0r3pwh01a7b"><span class="c4 c2">9. Data Security</span></h2>
    <p class="c1"><span class="c3">We implement appropriate technical and organizational measures to protect your
            personal information, including:</span></p>
    <ul class="c9 lst-kix_1casyz8x0m88-0 start">
        <li class="c0 li-bullet-0"><span class="c3">Access controls limiting who can view personal data</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Secure data storage practices</span></li>
    </ul>
    <p class="c1"><span class="c3">However, no method of transmission over the internet is 100% secure.</span></p>
    <h2 class="c8" id="h.j37me8uxofz0"><span class="c4 c2">10. Changes to This Policy</span></h2>
    <p class="c1"><span class="c3">We may update this privacy policy to reflect changes in our practices or applicable
            laws. When we make material changes, we will:</span></p>
    <ul class="c9 lst-kix_lwmjffcuipk-0 start">
        <li class="c0 li-bullet-0"><span class="c3">Update the &quot;Last updated&quot; date at the top</span></li>
        <li class="c0 li-bullet-0"><span class="c3">Notify users through the game or Steam platform when
                appropriate</span></li>
        <li class="c0 li-bullet-0"><span class="c3">For significant changes, provide advance notice where required by
                law</span></li>
    </ul>
    <h2 class="c8" id="h.9ybq4ukxmdn8"><span class="c4 c2">11. Contact Us</span></h2>
    <p class="c1"><span class="c3">If you have questions, concerns, or wish to exercise your privacy rights:</span></p>
    <ul class="c9 lst-kix_qaeuuzpu1qt0-0 start">
        <li class="c0 li-bullet-0"><span class="c2">Email:</span><span class="c3">&nbsp;spellmasons@gmail.com</span>
        </li>
        <li class="c0 li-bullet-0"><span class="c2">Response time:</span><span class="c3">&nbsp;We will respond to your
                request within the time frame required by applicable law, which is typically 30 days under GDPR and 45
                days under CCPA, subject to any permitted extensions.</span></li>
    </ul>
    <p class="c1"><span class="c3">For EU residents, you also have the right to lodge a complaint with your local data
            protection authority if you believe we have not addressed your concerns adequately.</span></p>
    <p class="c1 c10"><span class="c3"></span></p>
