<script>
    import { get } from "svelte/store";
    import Button from "./Button.svelte";
    import { SAVE_PREFIX } from "./globals.js";
    import { MULTIPLAYER_SERVER_CHOOSER, PLAY } from "./routes";
    import { connected, isInRoom } from "./stores";
    export let setRoute;
    let saveFiles = [];
    let saveFilesQuicksaves = [];
    let allSaveFiles = [];
    // Also used in main Spellmasons repo
    function parseSaveFile(saveFileName) {
        const [dateString, saveName] = saveFileName.split("-");
        const parsedDate = parseInt(dateString);
        // New parsing
        const [_wholeMatch, isQuicksave, date, gameName, extraInfo] =
            /(quicksave-)?(\d+)?-?(\w+)-?([\w\s]+)?/.exec(saveFileName);

        return {
            saveFileName,
            date:
                // New date parsing
                (date && parseInt(date)) ||
                // old date parsing
                (isNaN(parsedDate) ? 0 : parsedDate),
            isQuicksave: !!isQuicksave,
            displayName: isNaN(parsedDate)
                ? `${gameName}${extraInfo ? `-${extraInfo}` : ""}`
                : saveName !== ""
                  ? saveName
                  : new Date(parsedDate).toString(),
        };
    }
    function getSaveFiles() {
        allSaveFiles = Object.keys(localStorage)
            // Get all localStorage that are save files
            .filter((x) => x.includes(SAVE_PREFIX))
            // Get the name that doesn't include the save prefix
            .map((x) => x.split(SAVE_PREFIX).join(""))
            .map(parseSaveFile)
            // Have most recent at the top
            .sort((a, b) => b.date - a.date);
        saveFiles = allSaveFiles
            .filter((s) => !s.isQuicksave)
            .reduce(groupByDateReducer, {});
        saveFilesQuicksaves = allSaveFiles
            .filter((s) => s.isQuicksave)
            .reduce(groupByDateReducer, {});

        // Delete quicksaves that are > 2 days old
        const twoDaysAgo = new Date();
        twoDaysAgo.setDate(twoDaysAgo.getDate() - 2);
        allSaveFiles
            .filter((saveFile) => {
                return (
                    saveFile.isQuicksave && new Date(saveFile.date) < twoDaysAgo
                );
            })
            .forEach((saveFile) => {
                console.log("Cleaning up old save file:", saveFile);
                window.deleteSave(saveFile.saveFileName, true);
            });
    }
    const groupByDateReducer = (acc, saveFile) => {
        const date = new Date(saveFile.date).toLocaleDateString("en-US", {
            year: "numeric",
            month: "long",
            day: "numeric",
        });
        if (!acc[date]) {
            acc[date] = [];
        }
        acc[date].push(saveFile);
        return acc;
    };
    getSaveFiles();
</script>

<div class="flex">
    <aside>
        <div class="flex flex-direction-column gap-15 pad-20">
            <div class="flex align-items-center gap-20">
                <Button
                    on:click={() => {
                        if (
                            get(connected) &&
                            get(isInRoom) &&
                            !globalThis.player.lobbyReady &&
                            !globalThis.pie.soloMode
                        ) {
                            setRoute(MULTIPLAYER_SERVER_CHOOSER);
                        } else {
                            window.goBack();
                        }
                    }}
                    type="button"
                    text="🠔 Back"
                    class="sm"
                />
                <h1 class="aside-title">
                    {globalThis.i18n("Previous Games")}
                </h1>
            </div>
            {#if location.href.includes("localhost")}
                <Button
                    on:click={async () => {
                        const doReset = await window.Jprompt({
                            text: "Are you sure you want to delete all save files?",
                            yesText: "DELETE",
                            noBtnText: "cancel",
                            noBtnKey: "Escape",
                            // Ensure it shows up over menu z-index
                            forceShow: true,
                        });
                        if (doReset) {
                            for (let saveFile of allSaveFiles) {
                                window.deleteSave(saveFile.saveFileName, true);
                            }
                            getSaveFiles();
                        }
                    }}
                    type="button"
                    text="Delete All"
                />
            {/if}
        </div>
    </aside>

    <main class="flex">
        <div class="pad-20 flex flex-direction-column gap-15">
            <h2>Saved Games</h2>
            {#each Object.entries(saveFiles) as [date, listOfSaves]}
                <div>
                    {date
                        ? new Date(date).toLocaleDateString("en-US", {
                              year: "numeric",
                              month: "long",
                              day: "numeric",
                          })
                        : ""}
                </div>
                {#each listOfSaves as saveFile}
                    <div class="flex gap-15">
                        <Button
                            on:click={() => window.load(saveFile.saveFileName)}
                            type="button"
                            text={saveFile.displayName}
                            style="min-width:300px; overflow:hidden;"
                        />
                        <Button
                            on:click={() => {
                                window
                                    .deleteSave(saveFile.saveFileName)
                                    .then(() => {
                                        getSaveFiles();
                                    });
                            }}
                            type="button"
                            text={"X"}
                            style="width:65px"
                        />
                    </div>
                {/each}
            {/each}
        </div>
        <div class="pad-20 flex flex-direction-column gap-15">
            <h2>Quicksaves</h2>
            <div>
                {globalThis.i18n("quicksave-delete-note")}
            </div>
            {#each Object.entries(saveFilesQuicksaves) as [date, listOfSaves]}
                <div>
                    {date
                        ? new Date(date).toLocaleDateString("en-US", {
                              year: "numeric",
                              month: "long",
                              day: "numeric",
                          })
                        : ""}
                </div>
                {#each listOfSaves as saveFile}
                    <div class="flex gap-15">
                        <Button
                            on:click={() => window.load(saveFile.saveFileName)}
                            type="button"
                            text={saveFile.displayName}
                            style="min-width:300px; overflow:hidden;"
                        />
                        <Button
                            on:click={() => {
                                window
                                    .deleteSave(saveFile.saveFileName)
                                    .then(() => {
                                        getSaveFiles();
                                    });
                            }}
                            type="button"
                            text={"X"}
                            style="width:65px"
                        />
                    </div>
                {/each}
            {/each}
        </div>
    </main>
</div>

<style>
    main {
        max-height: 95vh;
        overflow: auto;
    }
    aside {
        border-right: 2px solid #efefef;
        width: 300px;
        /* Ensure that the options aside will become scrollable if the screen is too small */
        overflow-y: auto;
        /* Ensure it doesn't overlap with the border around the edges of the window */
        max-height: 90vh;
    }
</style>
