<script>
    import Button from "./Button.svelte";
    import ButtonSecondary from "./ButtonSecondary.svelte";
    import {
        SINGLE_PLAYER_OPTIONS,
        MODS,
        LOAD_GAMES,
        PLAY
    } from "./routes";
    export let setRoute;

    function chooseSingleplayer() {
        // Start music on button click to get past Firefox permissions
        window.playMusic();
        if(globalThis.isTutorialComplete()){
            // If difficulty options are available, allow chosing them
            setRoute(SINGLE_PLAYER_OPTIONS);
        }else{
            // Start singleplayer immediately
            // Connect to solo
            window.startSingleplayer(1).then(() => {
                window.syncConnectedWithPieState(true);
                setRoute(PLAY);
            });
        }
    }
</script>

<div id="main-menu">
    <div id="main-menu-inner" class="flex-menu">
        <h1>{globalThis.i18n('Singleplayer')}</h1>
        <Button
            on:click={() => {
                chooseSingleplayer();
            }}
            text="New Run"
            style="width:150%;"
            class="justify-center"
        />
        <Button
            on:click={() => setRoute(LOAD_GAMES)}
            text="Load"
            class="justify-center"
        />
        <Button
            on:click={() => setRoute(MODS)}
            text="Mods"
            class="justify-center"
        />
        <ButtonSecondary
            on:click={() => setRoute(PLAY)}
            text="🠔 Back"
            class="justify-center"
        />
    </div>
</div>

<style>
    .flex-menu {
        gap:20px;
    }

</style>